import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
//import { tokenNotExpired } from 'angular2-jwt';
import { JwtHelperService } from '@auth0/angular-jwt';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  authToken: any;
  user: any;

  constructor(private http: HttpClient, private decoder: JwtHelperService) { }

  registerUser(user){
    let header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.http.post<{message: string, success: boolean}>('http://localhost:3000/users/register', user, {headers: header})
    .pipe(map((res) => {
      return res;
    }));
  }

  authenticateUser(user){
    let header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.http.post<{msg: string, success: boolean, user: any, token: string}>
    ('http://localhost:3000/users/authenticate', user, {headers: header})
    .pipe(map((res) => {
      return res;
    }));
  }


  getProfile(){
    let header = new HttpHeaders();
    this.loadToken();
    header = header.append('Content-Type', 'application/json');
    header = header.append('Authorization', this.authToken);
    return this.http.get<{msg: string, success: boolean, user: any, token: string}>
    ('http://localhost:3000/users/profile', {headers: header})
    .pipe(map((res) => {
      return res;
    }));
  }

  loadToken(){
    const token  = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn(){
    const token  = localStorage.getItem('id_token');
    const isTokenExpired = this.decoder.isTokenExpired(token); //tokenNotExpired('id_token');
    return isTokenExpired;
  }
  
  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }


}
